import { useCallback, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import { useFormikContext } from 'formik';

import { useNavigationUtils } from '@mssgme/hooks';
import { useBlockEditContext } from './useBlockEditor';

export const useBlockChooser = (onCallback) => {
    const history = useHistory();
    const ref = useRef(onCallback);
    const { generateUrl } = useNavigationUtils();
    const { setCallback, setMixin, reset } = useBlockEditContext();
    const { setFieldValue } = useFormikContext();

    ref.current = onCallback;

    const handleCallback = useCallback(
        async (...args) => {
            ref.current && (await ref.current(...args));

            setTimeout(reset, 100);
        },
        [setCallback, setFieldValue]
    );

    return useCallback(() => {
        setCallback(handleCallback);
        setMixin({ nested: true });

        history.push(generateUrl('./add-block'));
    }, [history, generateUrl, handleCallback]);
};
